<template>
    <div class="articles">
        <div class="banner">
            <mt-swipe :auto="4000">
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/1.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/2.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/3.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/4.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/5.jpg" alt="">
                    </div>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="pdlr16">
            <div class="adhClassify mt10">
                <button type="button" @click="showClassify"><span>分类：</span><span>{{curType=='All'?'全部':(curType=='mini'?'小程序':curType)}}</span><img class="positTBCenter" src="@/assets/image/right.png" alt=""></button>
            </div>
        </div>
        <div class="hlist">
            <div class="hlist_box"  v-for="item in dataAll.article">
                <router-link :to="{path:'/detail',query:{id:item.id}}">
                    <img :src="domain+item.thumb" alt="">
                    <div>
                        <h4>{{item.title}}</h4>
                        <p><span>{{item.classify_tagname}}</span><span>{{item.publish_time}}</span></p>
                    </div>
                </router-link>
            </div>
        </div>
        <popList :popDatas="popData" :popStatus="popStatus" @popChange="changClassify" @popCancel="popCancel" @popConfirm="popConfirm"></popList>
    </div>
</template>
<script>
export default {
    name:'articles',
    data(){
        return{
            loadingStatus: false,
            data_type:'',
            popStatus: false,
             popData:{
                title:'分类',
                slots: [
                    {
                    flex: 1,
                    values: [],
                    className: 'slot1',
                    textAlign: 'center',
                    valueKey:'2015-02'
                    }
                ],
            },
            tagArr:[],
            pages: 1,//当前页数
            curType:'All',//分类类型
            dataAll:[],//列表数据
            domain: this.hosts,
            isLoading: false,//是否加载中
        }
    },
    
    mounted(){
        var _this = this;
        
        this.requests({reqUrl:'/api/index/classify_tags'}).then(function(res){
            console.log(res);
            var curdata = [];
            curdata[0] ='全部';
            var tagArr=[];
            tagArr[0] = 'All';
            for(var i=0;i<res.data.length; i++){
                curdata[i+1] = res.data[i].tags_name;
                tagArr[i+1] = res.data[i].tags;
            }
            _this.popData.slots[0].values = curdata;
            _this.tagArr =tagArr;
            console.log(tagArr);
            
        })
        this.ajax_req(1,'All');
        this.isApp?this.scrolls():'';
    },
    methods: {
        showClassify:function(){
            this.popStatus?(this.popStatus = false):(this.popStatus = true);
        },
        changClassify:function(e){
            // 选择分类
            console.log(e);
        },
        popCancel(e){
            this.popStatus = false;
        },
        popConfirm(e){
            // 确定分类
            console.log(e);
            this.curType = e.curValue[0];
            console.log(e.curValue[0]);
            var curTypes;
            for(var i=0; i<this.tagArr.length;i++){
                if(e.curValue[0] =='全部'){
                    curTypes = 'All';
                }else if(e.curValue[0] =='小程序'){
                    curTypes = 'mini';
                }else if(e.curValue[0]==this.tagArr[i]){
                    curTypes = e.curValue[0];
                }
            }
            this.curType = curTypes;
            this.ajax_req(1,curTypes);
            this.popStatus = false;
        },
        ajax_req:function(pages,artType){
            //  获取文章列表
            var _this = this;
            if(!this.isLoading){
                this.isLoading = true;
                if(pages==1?pages==1:pages<=this.dataAll.allpage){
                    this.requests({reqUrl:'/api/index/get_article?page_num='+pages+'&select_value='+artType}).then(function(ret){
                        if (ret) {
                            if(pages>1){
                                var oldList = _this.dataAll.article;
                                var curDatas = ret;
                                // if(ret.article.length>0){
                                    //  _this.showMsg({text:'数据数量：'+ret.article.length,times: 5000});
                                    for(var i=0;i<ret.article.length;i++){
                                        oldList.push(ret.article[i]);
                                        // _this.showMsg({text:'有push'+i,times: 1000});
                                    }
                                    
                                    // oldList.contact(ret.article);
                                    // _this.showMsg({text:oldList.length,times: 5000});
                                    curDatas.article = oldList;
                                    _this.dataAll = curDatas;
                                // }
                            }else{
                                _this.dataAll = ret;
                            }
                            _this.pages = pages;
                        }
                         _this.isLoading = false;
                    })
                }else{
                    _this.isLoading = false;
                    _this.showMsg({text:'没有更多了！',times: 3000});
                    
                }
            }
           
        },
        scrolls:function(){
            var _this = this;
            api.addEventListener({
                name:'scrolltobottom',
                extra:{
                    threshold: 0           
                }
            }, function(ret, err){
                 var reqPage = ++_this.dataAll.curpage;
                _this.ajax_req(reqPage,_this.curType);
            });
        },
    },
    activated(){
        // this.scrolls();
        
    },
}
</script>
<style>
    .banner{
        height: 200px;
    }
    .banbox img{
        width: 100%;
        height: auto;
    }
    .hlist{
        margin-top: 30px;
    }
    .hlist_box{
        padding: 20px;
        border-bottom: 1px solid rgb(240, 240, 240);
    }
    .hlist_box a{
        display: block;
        /* display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        flex-wrap: wrap; */
        color: rgb(69, 69, 69);
        position: relative;
        padding-left: 120px;
    }
    .hlist_box a img{
        display: block;
        width: 100px;
        height: 100px;
        object-fit:cover;
        /* margin-right: 20px; */
        position: absolute;
        top: 0;
        left:0;
    }
    .hlist_box a div{
        /* -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1; */
        min-height: 100px;
    }
    .hlist_box a h4{
        font-size: 16px;
        font-weight: normal;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
    .hlist_box a div p{
        margin-top: 10px;
        font-size: 14px;
    }
    .hlist_box a p span:first-of-type{
        background-color: #6cf;
        color: white;
        font-size: 12px;
        margin-right: 30px;
        padding: 2px 8px;
        border-radius: 4px;
    }


    .adhClassify {
        border: 1px solid rgb(240, 240, 240);
        position: relative;
        border-radius: 4px;
    }
    .adhClassify button{
       width: 100%;
       display: block;
       line-height: 50px;
       text-align: left;
       position: relative;
       padding: 0px 16px;
    }
    .adhClassify img{
        right: 16px;
        width: 16px;
        height: 16px;
    }
</style>


